const changePriorityLang = priority => {
  let result = '';

  switch (priority) {
    case 'without priority':
      result = ' ';
      break;

    case 'high':
      result = '';
      break;

    case 'medium':
      result = '';
      break;

    default:
      result = '';
  }

  return result;
};

export default changePriorityLang;
